export interface CellItemObject {
    goblin: string;
    pic: string;
    farming_pool: string;
    f_token: string;
    strategy: string;
    liquidate: string;
    withdraw: string;
    leverage: string;
}

const CellItemWidget: React.FC<{ data?: Global.GoblinVoList }> = ({data}) => {
    const {farms} = useGlobal()
    const farm: any = byIdgetItem(farms.farm_list, data?.goblin, 'goblin');
    const FarmsItem = byIdgetItem<FarmsItem>(farms.farm_list, data?.goblin, 'goblin');

    let history = useHistory();

    if (!farm || !FarmsItem) return <span/>;
    return (
        <CellItemStyle>

            <div className="one">
                <div className="label">
                    <div className="icons">
                        <img src={FarmsItem.tokenA_icon} alt=""/>
                        <img src={FarmsItem.tokenB_icon} alt=""/>
                    </div>
                    <div className="right">
                        <h2>{data?.farming_pool}</h2>
                        <h4>Yield Farming</h4>
                    </div>

                </div>

                <div className="income">
                    <h2>{farm.debt_apr} %</h2>

                    <h3>69.19 %</h3>
                </div>
            </div>

            <div className="msg">
                <h3>Yield Farming <span>{farm.yield_apr}%</span></h3>
                <h3>Trading Fees <span>{farm.trading_fees} %</span></h3>
                <h3>Naboo Rewards <span>{farm.token_rewards}%</span></h3>
            </div>

            <div className="underlying">
                <h4><img src={FarmsItem.tokenA_icon} alt=""/>
                    <span>{getBalanceNumber(farm.tokenA_amount)} {FarmsItem.tokenA_name}</span>
                </h4>
                <h4><img src={FarmsItem.tokenB_icon} alt=""/>
                    <span>{getBalanceNumber(farm.tokenB_amount)} {FarmsItem.tokenB_name}</span>
                </h4>
            </div>

            <div onClick={() => history.push(`/dashboard/farm/${data?.goblin}`,)}>
                <ButtonWidget/>
            </div>
        </CellItemStyle>
    );
}

