import {FarmPoolsStyle, StyledLink} from "./farm_pools";
import PositionsIcon from "../../assets/images/positions.png";
import React, {useContext, useEffect} from "react";
import {ButtonWidget} from "../../components/public/button_widget";
import styled from "styled-components";
import {byIdgetItem} from "../../global";
import {getBalanceNumber} from "../../utils/formatBalance";
import {useHistory, useParams} from "react-router-dom";
import {HttpStatus, useWait} from "../../utils/wait_fn";
import {HttpServer} from "../../net/http_server";
import {useActiveWeb3React} from "../../hook";
import {ModalContext} from "../../hook/model_provider";
import {CloseYourWidget} from "./close_your";
import {useGlobal} from "../../state/global/hooks";
import {Global} from "../../state/global/reducer";
import {Skeleton_bar} from "./Skeleton_bar";

const CellItemStyle = styled.div`
  background: #2D3340;
  border-radius: 6px;
  padding: 20px 18px;
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  cursor: pointer;

  > div {
    flex: 2;
  }

  > div:last-child {
    flex: 0;
    display: flex;

    > div {
      padding: 5px 10px;
    }

    div:last-child {
      margin-left: 5px;
    }
  }

  &:hover {
    background-color: rgba(61, 69, 87, 1);
  }

  .id {
    color: white;
  }

  .label {
    display: flex;
    flex: 3;

    .icons {
      display: flex;
      align-items: center;

      img {
        display: block;
        width: 28px;
        height: 28px;
      }

      img:last-child {
        margin-left: -5px;
      }
    }

    .right {
      margin-left: 10px;

      h2 {
        font-size: 12px;
        font-weight: 400;
        color: #FFFFFF;
        line-height: 1;
      }

      h4 {
        font-size: 10px;
        font-weight: 400;
        color: #9EA9B5;
      }
    }
  }


  .income {

    h2 {
      font-size: 14px;
      font-weight: 400;
      color: #FFFFFF;
      line-height: 1;
    }

    h3 {
      font-size: 16px;
      font-weight: 500;
      color: #F2F2F3;
    }

    h4 {
      font-size: 14px;
      font-weight: 400;
      color: #9EA9B5;
    }
  }


`

export interface PositionItemObject {
    id: string;
    goblin: string;
    tx_hash: string;
    position_value: number;
    total_debt: number;
}

const CellItemWidget: React.FC<{ data: PositionItemObject, coles: boolean }> = ({data: positionData, coles}) => {
    const {globalConfig, farms} = useGlobal()
    let history = useHistory();
    const {onPresent} = useContext(ModalContext);
    const GlobalItem: any = byIdgetItem<any>(globalConfig.goblin_list, positionData?.goblin, 'goblin');

    const FarmsItem = byIdgetItem<Global.FarmList>(farms.farm_list, positionData?.goblin, 'goblin');

    if (!positionData || !GlobalItem) return (<span/>);

    return (
        <CellItemStyle>
            <div className={'id'} style={{flex: 1}}>
                # {positionData?.id}
            </div>
            <div className="label">
                <div className="icons">
                    <img src={FarmsItem?.tokenA_icon} alt=""/>
                    <img src={FarmsItem?.tokenB_icon} alt=""/>
                </div>
                <div className="right">
                    <h2>{GlobalItem.farming_pool}</h2>
                    <h4>Yield Farming</h4>
                </div>
            </div>


            <div className="income">
                <h3>$ {getBalanceNumber(positionData['position_value'])}</h3>
            </div>

            <div className="income">
                <h3>$ {getBalanceNumber(positionData['total_debt'])}</h3>
            </div>
            <div className="income">
                <h3>{(positionData['position_value'] / positionData['total_debt']).toFixed(2)} %</h3>
            </div>
            <div>
                {/*<ButtonWidget text={'Close'} onClick={() => history.push(`/dashboard/close/${positionData?.id}`,)}/>*/}
                {!coles && <>
                    <ButtonWidget text={'Close'}
                                  onClick={() => onPresent(<CloseYourWidget positions={positionData}/>)}/>
                    {/*<ButtonWidget onClick={() => history.push(`/dashboard/farm/${positionData?.goblin}`,)} text={' '}/>*/}
                </>}
            </div>
        </CellItemStyle>
    );
}


export const PositionPage = () => {
    const {account} = useActiveWeb3React();

    const {request: position, data: positionListData, status: positionStatus} = useWait<any>(HttpServer.position);
    const {request: close, data: closeListData, status: closeStatus} = useWait<any>(HttpServer.position);
    let {type} = useParams<{ type: string }>();

    useEffect(() => {
        position(account,);
        close(account, 1);
    }, []);

    return (<FarmPoolsStyle>
        <div className="top">
            <h1 className="title">
                <img src={PositionsIcon} alt=""/>
                <span>Your Positions</span>
            </h1>
        </div>
        <div className="select_buttons">
            <StyledLink to={'./active'}>Active</StyledLink>
            <StyledLink to={'./inactive'}>Inactive</StyledLink>
        </div>
        <div className="list_title">
            <h3>
                <p style={{flex: "1"}}>#</p>
                <p style={{flex: "3"}}>Pool</p>
                <p>Position Value</p>
                <p>Borrow Credit</p>
                <p>Debt Ratio</p>
                <div style={{textAlign: "end"}}>
                    <p style={{flex: 0, width: "64px"}}>Action</p>
                </div>
            </h3>
        </div>


        <div>
            {/*{data &&  (type == 'active' ?data.position_list : data.close_list).map((ev: any) => <CellItemWidget key={ev.id} data={ev}/>)}*/}

            {
                type === 'active' && <>
                    {
                        positionListData?.position_list ? positionListData?.position_list.map((ev: any) =>
                                <CellItemWidget key={ev.id} data={ev} coles={false}/>) :
                            <p className={'not_tis'}>No active positions</p>
                    }
                </>
            }
            {
                type === 'inactive' && <>

                    {
                        closeListData?.close_list ? closeListData?.close_list.map((ev: any) => <CellItemWidget
                            coles={true}
                            key={ev.id} data={ev}/>) : <p className={'not_tis'}>No inactive positions</p>
                    }
                </>
            }
            {
                (positionStatus == HttpStatus.wait || closeStatus == HttpStatus.wait) && <Skeleton_bar/>
            }


        </div>
    </FarmPoolsStyle>)
}
