import Bnbimg from "../../assets/images/bnb.png";
import Btcimg from "../../assets/images/btc.png";
import {ButtonWidget} from "../../components/public/button_widget";
import React, {useCallback, useContext} from "react";
import {ApiType} from "../../type/liquidate_list";
import {byIdgetItem} from "../../global";
import {Global} from "../../state/global/reducer";
import {useGlobal} from "../../state/global/hooks";
import {getBalanceFormat} from "../../utils/formatBalance";
import {useBankContract, useGoblinContract} from "../../hook/useContract";
import {ModalContext} from "../../hook/model_provider";
import {calculateGasMargin} from "../../hook/useApproveCallback";
import {SendModal} from "../../components/modal/send_modal";

export const ExploreAllPositionsWidget: React.FC<{ props: ApiType.LiquidateList }> = ({props}) => {

    const {farms, globalConfig} = useGlobal();
    const {onPresent} = useContext(ModalContext);
    const farmsItem = byIdgetItem<Global.FarmList>(farms.farm_list, props?.goblin, 'goblin');
    const globalItem = byIdgetItem<Global.GoblinVoList>(globalConfig.goblin_list, props?.goblin, 'goblin');

    const bankContract = useBankContract(globalConfig.bank);

    const onDeposit = useCallback(async () => {
        if (bankContract) {
            const args: any = [
                props.id
            ];
            console.log(props.id)
            const estimatedGas = await bankContract.estimateGas.kill(...args);
            try {
                let data = await bankContract.kill(
                    ...args,
                    {gasLimit: calculateGasMargin(estimatedGas)}
                );
                return data.hash;
            } catch (e) {
                throw new Error(e.message)
            }
        }

        throw new Error('amount')

    }, [])


    const onSend = useCallback((...res) => {
        onPresent(<SendModal confirmationFn={() => onDeposit()}/>);
    }, [])

    if (!farmsItem || !globalItem) return <span/>
    return (<div className={'cell_item'}>
        <div className="pool">
            <div className="icons">
                <img src={farmsItem.tokenA_icon} alt=""/>
                <img src={farmsItem.tokenB_icon} alt=""/>
            </div>
            {globalItem.farming_pool}
        </div>
        <div className="tvl">
            {getBalanceFormat(props.position_value, 2)}
        </div>
        <div className="pending">
            {getBalanceFormat(props.total_debt, 2)}
        </div>
        <div className="action">
            <div>
                <ButtonWidget onClick={onSend} text={'Kill'}/>
            </div>
        </div>
    </div>)
}
