import React, {useCallback, useContext, useMemo, useState} from "react";
import {useBankContract, useTokenContract} from "../../hook/useContract";
import {useToken} from "../../hook/Tokens";
import {tryParseAmount} from "../../hook/tryParseAmount";
import {ApprovalState, calculateGasMargin, useApproveCallback} from "../../hook/useApproveCallback";
import {useSingleCallResult} from "../../state/multicall/hooks";
import BigNumber from "bignumber.js";
import {getHexNumber, numberToUint256} from "../../utils/formatBalance";
import ibUSDV from "../../assets/images/ibUSDV@2x.png";
import usdv from "../../assets/images/USDV@2x.png";
import {FormControl, MenuItem, Select} from "@material-ui/core";
import naboo_app_Earn from "../../assets/images/naboo_app_Earn@2x.png";
import {ButtonWidget} from "../../components/public/button_widget";
import styled from "styled-components";
import {useActiveWeb3React} from "../../hook";
import {SendModal} from "../../components/modal/send_modal";
import {ModalContext} from "../../hook/model_provider";
import {useGlobal} from "../../state/global/hooks";


const RightStyle = styled.div`

  .select {
    padding: 0 !important;
  }

  .select_last {
    padding: 6px !important;
    justify-content: start !important;

    img {
      margin-right: 10px;
    }
  }

  .select .MuiInput-underline:before {
    display: none;
  }

  .select .MuiSelect-icon {
    color: white;
  }

  .demo-simple-select {

  }

  .menu_inner {
    display: flex;
    align-items: center;
    font-size: 14px;
    font-weight: 500;
    color: #F2F2F3;
    line-height: 16px;

    img {
      width: 24px;
      height: 24px;
      margin-right: 10px;
    }
  }
`

export const RightWidget: React.FC = () => {

    const {account} = useActiveWeb3React()
    const {onPresent} = useContext(ModalContext);
    const {globalConfig} = useGlobal()
    const toeknContract = useTokenContract(globalConfig.token);
    const bankContract = useBankContract(globalConfig.bank);
    const token: any = useToken(globalConfig.token);
    const amountToApprove = tryParseAmount((1e7).toString(), token);
    const [approval, approveCallback] = useApproveCallback(amountToApprove, bankContract?.address);
    const totalToken = useSingleCallResult(bankContract, 'totalToken', []).result;


    const balance = useSingleCallResult(toeknContract, 'balanceOf', [account || '']).result;
    const totalBalance = useSingleCallResult(bankContract, 'balanceOf', [account || '']).result;

    const totalSupply = useSingleCallResult(bankContract, 'totalSupply', []).result;
    const [amount, setAmount] = useState<string>();
    const coins = [{
        name: "USDV",
        img: usdv
    }, {
        name: "ibUSDV",
        img: ibUSDV
    }];

    const [select_value, setSelect] = useState<number>(0);

    const receive = useMemo(() => {
        if (totalSupply && totalToken && amount) {
            if (select_value == 0) {
                return new BigNumber(amount).times(totalSupply.toString()).div(totalToken.toString()).toFixed(3).slice(0, -1)
            }
            return new BigNumber(amount).times(totalToken.toString()).div(totalSupply.toString()).toFixed(3).slice(0, -1)
        }
        return ''
    }, [amount, totalSupply, totalToken])


    const onDeposit = useCallback(async (amountToApprove, amount, approval, select_value) => {
        if (bankContract && amountToApprove && amount) {
            if (approval != ApprovalState.NOT_APPROVED) {
                const args: any = [
                    numberToUint256(amount).toFixed(),
                ];
                let fn = select_value == 0 ? 'deposit' : 'withdraw';
                const estimatedGas = await bankContract.estimateGas[fn](...args).catch(() => {
                    return bankContract.estimateGas[fn](...args)
                })

                try {
                    let data = await bankContract[fn](
                        ...args,
                        {gasLimit: calculateGasMargin(estimatedGas)}
                    );
                    return data.hash;
                } catch (e) {
                    throw new Error(e.message)
                }
            } else {
                throw new Error('Attempting to stake without approval or a signature. Please contact support.')
            }
        }

        throw new Error('amount')

    }, [amountToApprove, amount, approval]);

    const _setAmount = useCallback((scale: number) => {
        let _amount = getHexNumber(select_value == 0 ? balance : totalBalance, 2, 18, false);
        _amount = new BigNumber(_amount).times(scale);
        setAmount(_amount.toFixed())

    }, [balance, totalBalance, select_value])


    const onSend = useCallback((...res) => {
        onPresent(<SendModal confirmationFn={() => onDeposit(amountToApprove, amount, approval, select_value)}/>);
    }, [amountToApprove, amount, approval, select_value])


    return (
        <RightStyle className={'item right_input'}>
            <h1 className="title">
                <span>Your ibUSDV Holding</span>
            </h1>

            <div className={'inputCell inputCell_one'}>
                <div className="select">
                    {/*<img src={Btcimg} alt=""/>*/}


                    <FormControl fullWidth style={{paddingLeft: "10px"}}>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            label="Age"
                            defaultValue={0}
                            value={select_value}
                            onChange={(ev: any) => {
                                setSelect(ev.target.value)
                            }}
                        >
                            {coins.map((ev, index) => <MenuItem value={index} key={index}>
                                <div className="menu_inner">
                                    <img src={ev.img} alt=""/>
                                    {ev.name}
                                </div>
                            </MenuItem>)}
                        </Select>
                    </FormControl>
                </div>
                <div className="input">
                    <input type="text" key={'dasds'} value={amount} onInput={(ev: any) => {
                        setAmount(ev.target.value)
                    }}/>
                    <div className={'max_button'}>
                        {
                            [.25, .5, .75, 1].map((ev) => <span key={ev+'---x'} onClick={() => _setAmount(ev)}>{ev * 100}%</span>)
                        }
                    </div>

                </div>
                <img src={naboo_app_Earn} className={'naboo_app_Earn'} alt=""/>
            </div>


            <div className={'inputCell'}>
                <div className="select select_last">
                    <img src={coins[(select_value == 0) ? 1 : 0].img} alt=""/>

                    {coins[(select_value == 0) ? 1 : 0].name}
                </div>

                <div className="input">
                    <input type="text" disabled value={receive}/>
                </div>
            </div>

            <div className={'button_bottom'}>
                {
                    approval == 1 ? <ButtonWidget onClick={() => {
                        approveCallback()
                    }} text={'Approval'}/> : <ButtonWidget onClick={onSend}/>
                }
            </div>
        </RightStyle>
    );
}
