import {kai as NetAxios} from './api'
import BigNumber from "bignumber.js";


export class HttpServer {
    static account: string | null;

    /**
     * product
     * @param product
     */
    static pools(): Promise<any> {
        return NetAxios.get('/api/pool', {});
    }

    // /api/position?userId=0x34A3F3a756BFD1d6bb0cABa4A66ABd8a18f3C8bD&inActive=1
    // /api/position?userId=0x34A3F3a756BFD1d6bb0cABa4A66ABd8a18f3C8bD
    /**
     * product
     * @param product
     */
    static position(account: string, inActive: number | undefined): Promise<any> {
        return NetAxios.get(`/api/position`, {userId: account, inActive});
    }

    /**
     * product
     * @param product
     */
    static liquidate(): Promise<any> {
        return NetAxios.get(`/api/liquidate`, {});
    }


    /**
     * product
     * @param farm
     */
    static farm(): Promise<any> {
        return NetAxios.get(`/api/farm`, {});
    }


    /**
     * product
     * @param farm
     */
    static pool(): Promise<any> {
        return NetAxios.get(`/api/pool`, {});
    }


}
