import { createReducer, nanoid } from '@reduxjs/toolkit'
import { upData} from './actions'



export declare module Global {

    export interface GoblinVoList {
        goblin: string;
        pic: string;
        farming_pool: string;
        f_token: string;
        strategy: string;
        liquidate: string;
        withdraw: string;
        leverage: string;
    }

    export interface GlobalConfig {
        bank: string;
        token: string;
        total: number;
        goblin_list: GoblinVoList[];
    }

    export interface FarmList {
        goblin: string;
        dex: string;
        pair_name: string;
        pair_icon: string;
        yield_apr: string;
        trading_fees: string;
        token_rewards: string;
        tokenA_name: string;
        tokenB_name: string;
        tokenA_icon: string;
        tokenB_icon: string;
        tokenA_amount: string;
        tokenB_amount: string;
        leverage_cap: string;
        debt_apr: string;
    }

    export interface Farms {
        total: number;
        farm_list: FarmList[];
    }

    export interface RootObject {
        globalConfig: GlobalConfig;
        farms: Farms;
    }

}




const initialState: Global.RootObject = {
    globalConfig:{

        "bank": "0x95f09905D483d00bbceB6CaD00cC9C90e404f903",
        "token": "0xB7D5776AcB48b07A8A3580d589Eb832e7C1E905c",
        "total": 1,
        "goblin_list": []
    },
    farms: {
        "total": 1,
        "farm_list": []
    }
}

export default createReducer(initialState, builder =>
  builder
    .addCase(upData, (state, action) => {
        state.globalConfig = action.payload.globalConfig
        state.farms = action.payload.farms;
      // const { chainId, blockNumber } = action.payload
      // if (typeof state.blockNumber[chainId] !== 'number') {
      //   state.blockNumber[chainId] = blockNumber
      // } else {
      //   state.blockNumber[chainId] = Math.max(blockNumber, state.blockNumber[chainId])
      // }
    })
)
