import React, {createContext, Dispatch, SetStateAction, useEffect, useState} from 'react';
import styled from "styled-components";
import "codemirror/lib/codemirror.css";
import BgImg from "../assets/images/bg_img.jpg"
import {BarWidget} from "../components/header/bar";
import {HashRouter as Router, Redirect, Route, Switch} from "react-router-dom";

import {DashboardPage} from "./dashboard/dashboard";
import {StatusPage} from "./status/status_page";
import {EarnPage} from "./earn/earn";
import Popups from "../components/Popups";
import {ModelProvider} from "../hook/model_provider";
import {useGlobal, useUpData} from "../state/global/hooks";


const WapStyle = styled.div`


  .index_body {
    max-width: 1080px;
    margin: 0 auto;
    padding: 48px 0;
  }
`;


export const IndexPage: React.FC = () => {

    let [toggleSetDarkMode] = useUpData();
    const {globalConfig} = useGlobal();

    useEffect(() => {
        toggleSetDarkMode()
    }, []);


    return (
        <WapStyle>
            <Popups/>
            <ModelProvider>
                <Router>
                    <BarWidget/>
                    <div className="index_body">
                        <Switch>
                            <Route path="/dashboard/">
                                <DashboardPage/>
                            </Route>
                            <Route path="/status/">
                                <StatusPage/>
                            </Route>
                            <Route path="/earn/">
                                <EarnPage/>
                            </Route>
                            <Redirect to="/dashboard"/>


                        </Switch>
                    </div>
                </Router>

            </ModelProvider>

        </WapStyle>

    );
}

