const LOW = 1;
const LeftWidget = () => {
    let history = useHistory();
    let {goblin} = useParams<{ goblin: string }>();
    const {globalConfig, farms} = useGlobal();
    const GlobalItem: any = byIdgetItem(globalConfig.goblin_list, goblin, 'goblin');
    const FarmsItem = byIdgetItem<FarmsItem>(farms.farm_list, goblin, 'goblin');
    const bankContract = useBankContract(globalConfig.bank);
    const toeknContract = useTokenContract(globalConfig.token)
    const {web3, account} = useActiveWeb3React()
    const {onPresent} = useContext(ModalContext);


    const token: any = useToken(globalConfig.token);
    const amountToApprove = tryParseAmount((1e7).toString(), token);
    const [approval, approveCallback] = useApproveCallback(amountToApprove, bankContract?.address);
    const addTransaction = useTransactionAdder();
    const balance = useSingleCallResult(toeknContract, 'balanceOf', [account || '']).result;

    const [amount, setAmount] = useState<string>();
    const intervalFn = (x: number): any => {
        if (x == 0) return LOW;
        return ((GlobalItem.leverage - LOW) / 3 * x + LOW).toFixed(2).slice(0, -1);
    }

    const intervalMap: any = {
        'Low': 0,
        'Moderate': 1,
        'Moderate_': 2,
        'High': 3,
    }

    const [interval, setInterval] = useState('Low')


    const onWork = useCallback(async (amountToApprove, amount) => {
        if (bankContract && amountToApprove && amount) {

            if (approval != ApprovalState.NOT_APPROVED) {

                let ext = web3.eth.abi.encodeParameters(['address', 'uint256'], [GlobalItem.f_token, 0]);
                let strategy = GlobalItem.strategy;
                let data = web3.eth.abi.encodeParameters(['address', 'bytes'], [strategy, ext]);

                let _interval = parseFloat(intervalFn(intervalMap[interval])) - 1;
                let _amount = numberToUint256(amount).toFixed();
                const args: any = [
                    "0",
                    FarmsItem?.goblin,
                    `${_amount}`,
                    _interval ? `${new BigNumber(_amount).times(_interval).toFixed()}` : 0,
                    "0",
                    data
                ];

                const estimatedGas = await bankContract.estimateGas.work(...args).catch(() => {
                    return bankContract.estimateGas.work(...args)
                })

                let msg = await bankContract.work(
                    ...args,
                    {gasLimit: calculateGasMargin(estimatedGas)}
                );
                return msg.hash;
            } else {
                throw new Error('Attempting to stake without approval or a signature. Please contact support.')
            }
        }
    }, [interval])

    const onSend = useCallback((...res) => {
        onPresent(<SendModal confirmationFn={() => onWork(amountToApprove, amount)}/>);
    }, [amount, amountToApprove])


    if (!GlobalItem || !FarmsItem) return <span/>;


    return (
        <LeftStyle>
            <h1 className="title">Farm Pancake V2 {GlobalItem.farming_pool} Pool</h1>
            <div className="input_box">
                <div className="input_style">
                    <img src={FarmsItem.tokenA_icon} alt=""/>
                    <input value={amount} onInput={(ev: any) => {
                        setAmount(ev.target.value)
                    }} type="number"/>
                    <span>{FarmsItem.tokenA_name}</span>
                </div>
                <div className="input_buttons">
                    {
                        [0.25, 0.5, 0.75, 1].map((ev) => <div className="button" onClick={() => {
                            if (balance) {
                                let _amount: string = new BigNumber(ev).times(getBalanceNumber(balance[0]._hex, 2)).toString();
                                setAmount(_amount)
                            }
                        }}>
                            {ev * 100} %
                        </div>)
                    }
                </div>
            </div>

            <div className="leverages">
                <h2 className="tis">Leverage</h2>
                <div className="box">
                    <div className="line"/>
                    {Object.keys(intervalMap).map((key) => {
                        return <div className={`item ${interval == key && "ac"}`}
                                    onClick={() => {
                                        setInterval(key)
                                    }}
                                    key={key}>
                            <h1>{intervalFn(intervalMap[key])}X</h1>
                            <span>{key} risk</span>
                        </div>
                    })}
                </div>
            </div>

            <div className="summary">
                <h2 className="tis">Summary</h2>
                <h3>Assets supplied <span>0.000 {FarmsItem.tokenA_name} + 0.000 {FarmsItem.tokenB_name}</span></h3>
                <h3>Assets in position value <span>0.000 {FarmsItem.tokenA_name} + 0.000 {FarmsItem.tokenB_name}</span>
                </h3>
            </div>

            <div className="bottom_buttons">
                <div className={'button'} onClick={() => history.goBack()}>Back</div>
                {
                    approval == 1 ? <div className={'button'} onClick={() => {
                        approveCallback()
                    }}>Approval</div> : <div className={'button'} onClick={() => {
                        // addPopup(
                        //     {
                        //         txn: {
                        //             hash:"0xssssss",
                        //             success: true,
                        //             summary: "transactions[hash]?.summary"
                        //         }
                        //     },
                        //     "xxxxxasa"
                        // )
                        onSend()
                    }}>FARM</div>
                }

            </div>
        </LeftStyle>
    );
}

