import {useCallback, useMemo} from 'react'
import {useDispatch, useSelector} from 'react-redux'

import {AppDispatch, AppState} from '../index'
import {upData} from './actions'
import {HttpServer} from "../../net/http_server";


export function useUpData(): [() => void] {
    const dispatch = useDispatch<AppDispatch>()

    const toggleSetDarkMode = useCallback(async () => {
        let pools = await HttpServer.pools();
        let farm = await HttpServer.farm();
        dispatch(upData({
            globalConfig: pools,
            farms: farm
        },),)
    }, [dispatch])

    return [toggleSetDarkMode]
}


export function useGlobal() {
    const global = useSelector((state: AppState) => state.global)
    return useMemo(() => {
        return global;
    }, [global])
}
