import styled from "styled-components";
import React from "react";

const ButtonStyle = styled.div`
  background: #D0D4DC;
  border-radius: 12px;
  border: 1px solid rgba(226, 230, 235, 0.34);
  backdrop-filter: blur(31px);
  padding: 5px 20px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  color: #29303B;
`

export const ButtonWidget:React.FC<{text?: string, onClick?:Function}> = ({text, onClick}) => {
    return (
        <ButtonStyle onClick={() => {onClick && onClick()}}>
            {text || 'FARM'}

        </ButtonStyle>
    );
}
