export const CloseYourWidget: React.FC<{ onDismiss?: Function, positions: PositionItemObject }> = ({positions, onDismiss}) => {

    const {farms, globalConfig} = useGlobal();
    const farmsItem = byIdgetItem<FarmsItem>(farms.farm_list, positions?.goblin, 'goblin');
    const GlobalItem: any = byIdgetItem(globalConfig.goblin_list, positions?.goblin, 'goblin');

    const bankContract = useBankContract(globalConfig.bank);
    const {web3} = useActiveWeb3React()


    const token: any = useToken(globalConfig.token);
    const amountToApprove = tryParseAmount((1e7).toString(), token);
    const [approval, approveCallback] = useApproveCallback(amountToApprove, bankContract?.address);
    const [type, setType] = useState(0);



    const onWork = useCallback(async () => {
        if (bankContract && amountToApprove) {

            if (approval != ApprovalState.NOT_APPROVED) {

                let ext = web3.eth.abi.encodeParameters(['address', 'uint256'], [GlobalItem.f_token, 0]);

                let strategy = type == 1 ? GlobalItem.withdraw : GlobalItem.liquidate;
                let data = web3.eth.abi.encodeParameters(['address', 'bytes'], [strategy, ext]);

                const args: any = [
                    positions.id,
                    farmsItem?.goblin,
                    "0",
                    "0",
                    positions.position_value,
                    data
                ];
                const estimatedGas = await bankContract.estimateGas.work(...args).catch(() => {
                    return bankContract.estimateGas.work(...args)
                })

                let a = await bankContract.work(...args, {gasLimit: calculateGasMargin(estimatedGas)});

                return a.hash

            } else {
                throw new Error('Attempting to stake without approval or a signature. Please contact support.')
            }
        }
    }, [amountToApprove])

    const {request,status, data} = useWait(onWork);


    return (
        <TransactionModal onDismiss={onDismiss} txHash={data} status={status}>
            <WapStyle>
                <h2>Close Your Position</h2>
                <h3>Position Value <span>{getBalanceFormat(positions.position_value)}</span></h3>
                <h3>Total debt <span>{getBalanceFormat(positions.total_debt)}</span></h3>
                {/*<h3>You’re receiving <span>2.390 XXX</span></h3>*/}

                <div className="bottom">
                    <h4> you like to use？</h4>
                    <div className="buttons">
                        {[0, 1].map((ev) => <div
                                className={`button ${ev == type && 'ac'}`}
                                onClick={() => setType(ev)}
                            >{ev}</div>
                        )}
                    </div>

                    <div className="bottom_buttons">
                        <div className={'button'} onClick={() => onDismiss && onDismiss()}>Back</div>
                        {
                            approval == 1 ? <div className={'button'} onClick={() => {
                                approveCallback()
                            }}>Approval</div> : <div className={'button'} onClick={() => {
                                request('')
                            }}>Reinvest</div>
                        }

                    </div>
                </div>
            </WapStyle>

        </TransactionModal>
    );
}
